import PropTypes from 'prop-types';
import React from 'react';
import PolarBankLogo from '../images/polarbank.png';
import OwlLogo from '../images/OwlConserv.png';
import InteracLogo from '../images/interac-logo.svg';
import { makeStyles } from '@material-ui/styles';
import classnames from 'classnames';
import { Theme } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) => ({
  header: {
    display: 'flex',
    overflow: 'hidden',
    maxHeight: '10vh',
    justifyContent: 'space-around',
    padding: '0.5rem',
    marginTop: '0.5rem',
    marginBottom: '0.5rem',
    [theme.breakpoints.down('md')]: {
      padding: '0.4rem',
      paddingLeft: '0.75rem',
      paddingRight: '0.75rem',
      marginTop: '0.5rem',
      marginBottom: '0.5rem',
    },
  },
  img: {
    display: 'flex',
    backgroundSize: 'cover',
    objectFit: 'scale-down',
  },
  leftLogos: {
    flex: 6,
    display: 'flex',
  },
  OwlLogo: {
    maxWidth: '33%',
    flex: 1,
  },
  PolarBankLogo: {
    maxWidth: '40%',
    flex: 1,
  },
  interacLogo: {
    maxWidth: '28%',
    flex: 1,
  },
}));

const Header = ({ siteTitle }: { siteTitle: string }) => {
  const classes = useStyles();
  return (
    <header className={classes.header}>
      <div className={classes.leftLogos}>
        <img
          className={classnames(classes.img, classes.interacLogo)}
          src={InteracLogo}
          style={{ padding: '0.25em' }}
        ></img>
        <img
          className={classnames(classes.img, classes.PolarBankLogo)}
          src={PolarBankLogo}
        ></img>
        <img
          className={classnames(classes.img, classes.OwlLogo)}
          src={OwlLogo}
        ></img>
      </div>
    </header>
  );
};

Header.propTypes = {
  siteTitle: PropTypes.string,
};

Header.defaultProps = {
  siteTitle: ``,
};

export default Header;
